import { Box } from '@mui/system'
import React from 'react'
import ActionCard from '../ActionCard/ActionCard'
import Grid from '@mui/material/Grid'
import {Link} from 'react-router-dom'

const style = {
    display: "flex",
    padding: "10px",
    flexWrap: "wrap"
}

const boxCardStyle = {
    margin: "auto"
}

function CardGallery() {
    return (
        <Box style={style}>
            {/* <Box style={{margin: "auto"}}>
                <ActionCard 
                title={"Tools"} 
                description={"Tools used"} 
                image={""}
                />
            </Box>

            <Box style={{margin: "auto"}}>
                <ActionCard 
                title={"Tools"} 
                description={"Tools used"} 
                image={""}
                />
            </Box> */}
            <Grid 
            container
            justifyContent={"space-evenly"}
            >
                <Grid item>
                    <Link to="/tools">
                    <Box style={boxCardStyle}>
                        <ActionCard 
                        title={"Tools"} 
                        description={"Tools used"} 
                        image={""}
                        />
                    </Box>
                    </Link>
                </Grid>

                <Grid item>
                    <Link to='/work_experience' underline="none"> 
                    <Box style={boxCardStyle}>
                        <ActionCard 
                        title={"Work Experience"} 
                        description={"Work Experience"}
                        />
                    </Box>
                    </Link>
                </Grid>

                <Grid item>
                    <Link to="/other_projects">
                    <Box style={boxCardStyle}>
                        <ActionCard 
                        title={"Other Projects"}
                        description={"Checkout my independant projects"}
                        image={""}
                        />
                    </Box>
                    </Link>    
                </Grid>

                <Grid item>
                    <Link to="/contact">
                    <Box style={boxCardStyle}>
                        <ActionCard 
                        title={"Contact Me"}
                        description={"Ways of contacting me"}
                        image={""}
                        />
                    </Box> 
                    </Link>
                </Grid>
            </Grid>
        </Box>
    )
}

export default CardGallery
