import Typography from '@mui/material/Typography'
import React from 'react'
import './OtherProjectsView.css'
import Card from '@mui/material/Card';
import CardContent from '@mui/material/CardContent';
import CardMedia from '@mui/material/CardMedia';
import { CardActionArea } from '@mui/material';
import VigilanceIcon from './images/Slide1.jpg';
import {Link} from 'react-router-dom'
import TitanicImg from './images/titanic.jpg';
import TitanicPDF from '../TitanicView/titanic.pdf';
import Box from '@mui/material/Box'

function card(title, description, image, link){

  return (
    <Link to={link} target="_blank">
    <Card sx={{ maxWidth: 400}}>
      <CardActionArea>
        <CardMedia
          component="img"
          height="140"
          image={image}
          alt="green iguana"
        />
        <CardContent>
          <Typography gutterBottom variant="h5" component="div">
            {title}
          </Typography>
          <Typography variant="body2" color="text.secondary">
            {description}
          </Typography>
        </CardContent>
      </CardActionArea>
    </Card>
    </Link>
  );
}

function OtherProjectsView() {
    return (
        <div>
          <Box className="box">
            <h1 className='text'>OtherProjectsView</h1>

            <h2 className="text">1. Vigilance depoartment app</h2>
            {card("Vigilance App", "This is the vigilance app", VigilanceIcon, "vigilance_app")}

            <br/>

            <h2 className="text">2. Titanic Survivor Prediction Model</h2>
            {card("Titanic Project", "This is titanic prediction model", TitanicImg, TitanicPDF)}
          </Box>
        </div>
    )
}

export default OtherProjectsView
